.chat_page_container {
    padding: 5px;
    width: 100%;
    height: calc(100% - 4rem);
    /* background-color: red; */
    overflow-y: scroll;
    position: fixed;
}

.chat_page_container input{

    color: black;
    background: linear-gradient(45deg, rgb(115, 115, 222), rgb(104, 230, 196));
    width: calc(100% );
    
}
.chat_controls h3 {
    margin-bottom: 0px;
}
.chat_controls {
    width: 95%;
    position: fixed;
    display: flex;
    flex-direction: row;
    bottom: 0%;
    left: 5px;
}
.message_container{
    overflow-y: scroll;
    position: fixed;
    top: 0%;
    left: 5px;
    height: calc(100vh - 7rem);
    width: calc(100% - 10px);
}
#send {
    background-color: white;    
    color: rgb(20, 9, 131);
    padding: 5px;
    border-radius: 5px;
    font-size: larger;
    height: 1.5rem;
    margin-left: 5px;

}